/* Dashboard.css */
/* ... existing styles ... */


.grandfatherbox {
    color: #C5C5D3;
    width: 95vw;
    height: 120vh;
    /* margin-bottom: 200px; */
}

.Dashboard {
    border-radius: 8px;
}

.GoalInput {
    display: flex;
    margin-bottom: 20px;
}

.GoalInput input {
    flex-grow: 1;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #bcbcbc;
    background-color: #1E1E26;
    color: white;
    border-radius: 5px;
}

.GoalInput button {
    padding: 10px;
    width: 10vw;
    height: 37px;
    background-color: #49495C;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 6px;
}

.GoalInput button:hover {
    background-color: #D6496B;
}

/* Container.css */
.Container {
    background-color: #333;
    padding: 20px;
    /* Add additional styles for the container here */
}

.Sidebar {
    width: 25vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 8px;
    overflow: hidden;
    margin: 5px;
    background: #1E1E26;
    border: 1px solid rgba(255, 255, 255, 0.4);
    /* Subtle border */
    box-shadow: 0 0px 6px rgba(255, 255, 255, 0.1);
    /* Optional shadow for depth */
    padding: 20px;
    /* Optional padding for inner content */
}


.Sidebar .active {
    background-color: #D6496B;
    /* Different background color for active name */
    font-weight: bold;
    /* Make the active name bold */
}

.MainContent {
    width: 75vw;
    border-radius: 8px;
    overflow: hidden;
    margin: 5px;
    background: #1E1E26;
 
    border: 1px solid rgba(255, 255, 255, 0.4);
    /* Subtle border */
    box-shadow: 0 0px 6px rgba(255, 255, 255, 0.1);
    /* Optional shadow for depth */
    padding: 20px;
    /* Optional padding for inner content */
}

.MainContent .ActiveName {
    font-size: 20px;
    /* Increase the font size for the active name */
    margin-bottom: 20px;
    /* Add some margin below the active name */
}

form {
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.Sidebar button:hover {
    transform: scale(1.02);
    /* Enlarge the button by 2% */
    background-color: #4A4A5D;
    /* Subtle color shift */
}

.Sidebar button {
    border-radius: 5px;
    width: 23vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #49495C;
    color: #C5C5D3;
    transition: transform 0.2s, background-color 0.2s;
    /* Transition effect for smooth enlargement and color shift */
}
.Sidebar button.active:hover {
    transform: scale(1.02); 
    background-color: #D6496B; /* Reset the background color to the active button's color */
}
.refresh-icon {
    transition: transform 0.3s ease;
  }
  
  .refreshing .refresh-icon {
    transform: rotate(360deg);
    animation: spin 1s linear infinite; /* Adjust the duration as needed */
  }
  
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  