/* App.css */
.App {
  background-color: #181818;
  color: white;
  height: 105vh;
  width: 100%;
  overflow: hidden;
}

/* Dashboard.css */
.Dashboard {
  display: flex;
  height: 100%;
}

.Sidebar {
  width: 40%;
  background-color: #222;
  overflow-y: scroll;
  padding: 20px;
}

.Sidebar button {
  display: block;
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  background-color: #333;
  color: white;
  border: none;
  text-align: left;
  cursor: pointer;
}

.Sidebar button:hover {
  background-color: #444;
}

.MainContent {
  width: 60%;
  background-color: #282828;
  padding: 20px;
  overflow-y: scroll;
}
